@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap");

body,
h1,
h2,
h3,
h4,
div,
p,
a {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Comfortaa", cursive;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #d9ccc1;
}
.wrapper {
  max-width: 980px;
}

header {
  display: flex;
  flex-direction: column;
  height: 100%;
  nav {
    display: flex;
    gap: 20px;
    height: 80px;
    align-items: center;
    a {
      padding: 5px;
      transition-duration: 0.3s;
      font-size: 16px;
      font-weight: 800;
      text-align: center;

      &:hover {
        border-bottom: 3px solid #8c503a;
        transition-duration: 0.3s;
      }
    }
  }
  .header-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    img {
      width: 100%;
    }
    nav {
      max-width: 500px;
    }
  }
  .right-header {
    display: flex;
    flex-direction: column;
    height: 85%;
    justify-content: space-between;
    align-items: center;
  }
}
.read-more {
  border-radius: 5px;
  padding: 15px;
  text-align: center;
  background-color: #bf6415;
 

  transition-duration: 0.3s;
  
  a{
    color: #d9ccc1;
    font-size: 16px;
  font-weight: 800;
  }
  &:hover {
    transition-duration: 0.3s;
    background-color: #8c503a;
  }
}

h1 {
  text-align: center;
  margin: 20px 0px;
  color: #024059;
}
p {
  color: #d9ccc1;
  margin: 20px 0px;
  text-align: justify;
  text-indent: 1.5rem;
  line-height: 1.5rem;
  font-weight: 600;
}
h2 {
  text-align: center;
  margin: 10px 0px;
  background-color: #8c503a;
  padding: 20px;
  border-radius: 10px;
}

section {
  background-color: #024059;
  padding: 20px;
  border-radius: 10px;
  margin-top: 20px;
  color: #d9ccc1;
  img {
    max-width: 100%;
  }
}
summary {
  h2,
  span {
    cursor: pointer;
  }
}
p {
  code {
    font-size: 20px;
  }
}
ul {
  li {
    list-style: none;
    margin-bottom: 10px;
  }
}

.text-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contacts {
  display: flex;
  gap: 20px;
  i {
    font-size: 30px;
    color: #8c503a;
  }
}
h4 {
  color: #024059;
  font-size: 18px;
  padding-bottom: 20px;
}

a {
  text-decoration: none;
  color: #8c503a;
}
.copyright {
  font-size: 12px;
  color: #024059;
  text-align: center;
}
.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}
table {
  // overflow-x:auto
  th,
  td {
    padding: 5px;
    border: 2px solid #024059;
  }
}
.code {
  background-color: #282c34;
  padding: 20px;
  margin: 20px 0px;
  border-radius: 10px;
  color: rgb(38, 110, 242);
  position: relative;
}
.tag {
  color: #e06c75;
}
.attr {
  color: #bf6415;
}
.tags-main {
  details[open] {
    max-width: 960px;
  }
}

.run-code {
  position: absolute;
  right: 20px;
  top: 20px;
  text-decoration: none;
  color: #d9ccc1;
  font-size: 20px;
}
.semantic-main {
  section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.dropdown {
  cursor: pointer;
  text-align: center;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #d9ccc1;
  border: 1px solid #8c503a;
  border-radius: 5px;
  max-width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  color: #024059;
  padding: 10px 20px;
  text-decoration: none;
  display: block;
  text-align: center;
}

.topnav a:hover,
.dropdown:hover .dropbtn {
  background-color: #555;
  color: white;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

#menu__toggle {
  opacity: 0;
}

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}
#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}
#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
#menu__toggle:checked ~ .menu__box {
  visibility: visible;
}

.menu__btn {
  display: flex;
  align-items: center;
  position: fixed;
  top: 20px;
  right: 20px;
  width: 26px;
  height: 26px;
  cursor: pointer;
  z-index: 2;
}
@mixin spanBtn{
  display: block;
  position: absolute;

  width: 100%;
  height: 2px;

  background-color: #616161;

  transition-duration: 0.25s;
}

.run-code-mobile{
  margin-left: 20px;
  color: #266EF2;
  display: none;
}

@media (max-width: 900px) {
  header {
    nav {
      gap: 10px;
    }
    h1 {
      font-size: 1.5rem;
    }
    .fa-caret-down,
    .dropdown:hover .dropdown-content {
      display: none;
    }
  }
  section,
  details[open] {
    margin: 10px 20px;
  }
  .scroll {
    background-color: #282c34;
  }
  .run-code {
    font-size: 1rem;
  }
}
@media (max-width: 750px) {
  header + footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 0;
  }
}

@media (max-width: 650px) {
  header {
    h1 {
      font-size: 1.2rem;
    }
  }
  .menu__btn {
    span {
      @include spanBtn;
  
      &::before {
        content: "";
        top: -8px;
        @include spanBtn
      }
      &::after {
        content: "";
        top: 8px;
        @include spanBtn
      }
    }
  }
  
  .menu__box {
    display: flex;
    position: absolute;
    flex-direction: row;
    visibility: hidden;
    top: 0;
    right: 0;
 
    padding-right: 40px;
    

    width: 100%;
    background-color: #d9ccc1;
  border: 1px solid #8c503a;
  border-top-left-radius: 10px;   
  border-bottom-left-radius: 10px;   
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
  
    
  }
  .code{
    width: min-content;
  }
}
@media (max-width: 540px) {
  .menu__box {  
    flex-direction: column;
    width: 200px;
    height: 240px;
    z-index: 1;
    padding-right: 20px;
  }
.code{
  display: none;

}
.run-code-mobile{
  display: inline;
}
  .home-page{
    img{
      display: none;
    }
    .header-content{
      display: flex;
      flex-direction: column;
      height: 400px;
      
    }
    .right-header{
      height: 100%;
      justify-content: space-evenly
    }
  }
  
}